.tasks {

	&:not(:last-of-type) {
		margin-bottom: 40px;
	}

	a {
		text-decoration: none;
	}

	&__title {
		font-family: Montserrat;
		font-weight: bold;
		font-size: 32px;
		line-height: 39px;
		color: #64C4ED;
		padding-bottom: 20px;
		border-bottom: 1px solid #f2f2f2;

		&:hover {
			img {
				opacity: 0.2;
			}
		}

		img {
			opacity: 0;
			cursor: pointer;
			margin-left: 15px;

			&:hover {
				opacity: 0.8;
			}
		}
	}

	&__form {
		margin-top: 20px;

		&-block {
			.button {
				margin-right: 10px;
				margin-top: 15px;
			}
		}

		&-new {
			display: flex;
			align-items: center;
			cursor: pointer;
			opacity: 0.4;
			transition: opacity 0.15s ease-in-out;

			&:hover {
				opacity: 0.8;
			}

			img {
				width: 16px;
				height: 16px;
				margin-left: 4px;
			}

			span {
				font-size: 16px;
				margin-left: 19px;
			}
		}
	}

	&__items {
		margin-top: 30px;
		h2 {
			font-family: Montserrat;
			font-weight: bold;
			font-size: 22px;
			color: #c9d1d3;
			text-align: center;
			position: absolute;
			left: 50%;
			top: 50%;
		}
		&-row {
			display: flex;
			align-items: center;
			margin-bottom: 15px;

			&:hover &-actions {
				opacity: 1;
			}

			&-actions {
				display: flex;
				opacity: 0;
				transition: opacity 0.15s ease-in-out;

				div {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 32px;
					height: 32px;
					background-color: lighten(#f4f6f8, 2%);
					border-radius: 6px;
					margin-left: 5px;
					cursor: pointer;

					&:hover {
						background-color: darken(#f4f6f8, 2%);
						img {
							opacity: 0.9;
						}
					}

					svg {
						width: 11px;
						height: 11px;
						opacity: 0.4;
					}
				}
			}

			p {
				margin-left: 15px;
				width: 100%;
				font-size: 16px;
				border: 0;
			}
		}
	}

	.checkbox {
		input {
			display: none;
		}

		svg {
			transition: opacity 0.15s ease-in-out;
			path {
				stroke: #f2f2f2;
			}
		}

		// сосдений элемент input справа,
		// при условии, что чекбокс нажат
		input:checked + label {
			background-color: #4DD599;
			border-color: #4DD599;

			svg {
				opacity: 1;
				path {
					stroke: #fff;
				}
			}
		}

		&:hover {
			label {
				background-color: #f2f2f2;
				border-color: #f2f2f2;
				svg {
					opacity: 1;
					path {
						stroke: #b2b2b2;
					}
				}
			}
		}

		label {
			cursor: pointer;
			display: flex;
			align-items: center;
			justify-content: center;
			border: 2px solid #e8e8e8;
			border-radius: 30px;
			width: 20px;
			height: 20px;
			transition: background-color 0.15s ease-in-out,
				border-color 0.15s ease-in-out;

			svg {
				opacity: 0;
			}
		}
	}
}